.app-bar__link{cursor:pointer;background:transparent;border:none;font-size:1em;outline:none;padding:0;text-decoration:none;text-decoration:none;color:#fff;width:3em;height:3em;display:inline-flex;align-items:center;justify-content:center;position:relative}.app-bar__link:hover::after{opacity:.08}.app-bar__link::after{position:absolute;top:0;left:0;width:100%;height:100%;background-color:#fff;content:"";opacity:0;transition:opacity 15ms linear;box-sizing:border-box}.app-bar__link:after{border-radius:50%}.app-bar{position:fixed;z-index:1;top:0;left:0;right:0;height:4em;background-color:#6200ee;box-shadow:0px 2px 4px -1px rgba(0,0,0,.2),0px 4px 5px 0px rgba(0,0,0,.14),0px 1px 10px 0px rgba(0,0,0,.12);display:flex;justify-content:space-between;align-items:center}.app-bar__links{flex-shrink:0}.app-bar__kontokorrent-select{margin-left:1em;min-width:6em}.body--app-bar{margin-top:4em}