.dialog{position:fixed;top:50vh;left:50vw;transform:translate(-50%, -50%);background-color:#fff;box-shadow:0px 2px 4px -1px rgba(0,0,0,.2),0px 4px 5px 0px rgba(0,0,0,.14),0px 1px 10px 0px rgba(0,0,0,.12);border-bottom-left-radius:3px;border-bottom-right-radius:3px;margin-top:-1px;min-width:15em;padding:0 1em .5em 1em;z-index:1}.dialog__button-pane{display:flex;flex-direction:row;justify-content:flex-end}.dialog__backdrop{position:fixed;top:0;bottom:0;left:0;right:0;background-color:#000;opacity:.1;-webkit-animation:backdrop-open .08s cubic-bezier(0, 0, 0.2, 1);animation:backdrop-open .08s cubic-bezier(0, 0, 0.2, 1);will-change:opacity;z-index:1}@-webkit-keyframes backdrop-open{0%{opacity:0}100%{opacity:.1}}@keyframes backdrop-open{0%{opacity:0}100%{opacity:.1}}